import colors from '../colors'
import posed from "react-pose";

const Square = posed.div({
  idle: { scale: 1 },
  hovered: { scale: 1.05 }
});

class Project extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      h: false,
    }
  }

  render() {
    return(
      <Square
        pose={this.state.h? "hovered" : "idle"}
        onMouseEnter={() => this.setState({ h: true })}
        onMouseLeave={() => this.setState({ h: false })}>
        {(this.state.h ? (
          <div className="has-text-white has-text-centered is-flex" style={{flexDirection: "column", justifyContent: "center", height: 300, width: 200, borderRadius: 15, backgroundColor: colors.b3, padding: 10}}>
            <p className="is-size-5"> <b> Project Name </b> </p>
            <p className="is-size-6"> company desc </p>
            <p className="is-size-6"> project proposal </p>
            <p className="is-size-6"> deliverables </p>
            <p className="is-size-6"> impact </p>
          </div>) : (
          <div style={{height: 300, width: 200, borderRadius: 15, backgroundColor: colors.g3}}>
          </div>))}
      </Square>
    );
  }
}

export default Project;
